<template>
  <div :class="!skinOff && 'link'">
    <button v-if="to === ''" v-bind="$attrs">
      <slot></slot>
    </button>
    <a v-else-if="!router" :href="to" v-bind="$attrs">
      <slot></slot>
    </a>
    <router-link v-else :to="to" v-bind="$attrs">
      <slot></slot>
    </router-link>
  </div>
</template>

<script>
export default {
  inheritAttrs: false,
  props: {
    to: {
      validator: v => /^(?:\/|#|(?:ht|f)tps?:\/\/).*/i.test(v),
      default: ''
    },
    router: {
      type: Boolean,
      default: false
    },
    skinOff: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style scoped>

.link * {
  display: block;
  border: none;
  border-bottom: 1px solid;
  padding: 0;
  background: transparent;
  color: #AFAFAF;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  cursor: pointer;
}

.link:hover * {
  color: #90CAFF;
}
</style>
