<template>
  <div class="cryptostarter">
    <div class="title">{{ $localizeFilter('cryptostarter_title') }}</div>
    <div class="row-wrapper">
      <div class="my-balance">
        <p class="my-balance-link" @click.stop="$router.push('/cabinet/myBusinessBalance')">{{
          $localizeFilter('my_balance') }}
        </p>
      </div>
      <LargeToggle :title="null" :variants="businessTypes" :selected="selectedBusinessType" @toggle="toggle"
        class="toggle" />
    </div>

    <div class="info-wrapper" v-if="this.cryptoStartShare">
      <img src="../assets/newImages/crown.svg" alt="" class="crown">
      <p class="info">{{ $localizeFilter('result_crypto_start2', [this.cryptoStartShare]) }}</p>
    </div>

    <div class="businesses-list" v-if="selectedBusinessType === 'all_businesses'">
      <div class="business" v-for="(business, idx) in businesses" :key="idx">
        <Business :business="business" @participate="participateFromBusiness" />
      </div>
    </div>
    <div class="businesses-list" v-else-if="selectedBusinessType === 'my_businesses' && this.myBusinesses.length === 0">
      <div class="hint-wrapper">
        <div class="hint-content">
          <img class="hint-image" src="../assets/newImages/hint-icon.svg" alt="">
          <p class="hint-text">{{ $localizeFilter('my_businesses_hint') }}</p>
        </div>
      </div>
    </div>
    <div class="my-businesses-list" v-else-if="selectedBusinessType === 'my_businesses' && this.myBusinesses.length >= 0">
      <div class="business" v-for="(myBusiness, idx) in myBusinesses" :key="idx">
        <myBusiness :myBusiness="myBusiness" />
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import LargeToggle from "@/components/LargeToggle.vue"
import Business from '../components/Business.vue'
import myBusiness from '../components/myBusiness.vue'

export default defineComponent({
  props: {
    businessTypeOverride: {
      type: String,
      default: null
    }
  },
  components: {
    LargeToggle,
    Business,
    myBusiness,
  },
  data: () => ({
    businessTypes: ['all_businesses', 'my_businesses'],
    selectedBusinessType: 'all_businesses',
  }),
  watch: {
  },
  computed: {
    businesses() {
      return this.$store.getters.businessesList
    },
    myBusinesses() {
      return this.$store.getters.myBusinessesList
    },
    profile() {
      return this.$store.getters.profile || ''
    },
    profileLastName() {
      return this.profile.lastName || ''
    },
    cryptoStartShare() {
      // return '10.03'
      return this.profile.cryptoStartShare ? (this.profile.cryptoStartShare / 100000000).toFixed(8) : 0
    },
  },
  methods: {
    toggle(value) {
      this.selectedBusinessType = this.businessTypes[value],
        this.$router.replace({ query: { type: this.selectedBusinessType } })
    },
    async getBusinesses() {
      const response = await this.$store.dispatch('getBusinessesList')
    },
    async getMyBusinessesList() {
      const response = await this.$store.dispatch('getMyBusinessesList')
    },
  },
  async mounted() {
    this.isMounted = true
    if (this.businessTypeOverride) {
      this.selectedBusinessType = this.businessTypeOverride
    }
    await this.getBusinesses()
    await this.getMyBusinessesList()
  },
}
)
</script>

