<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '../filters/localize.filter'
import anime from 'animejs/lib/anime.es.js';
import ElButton from '../elements/ElButton.vue';
import ParticipateModal from '../components/newModals/ParticipateModal.vue'


export default defineComponent({
  props: {
    business: {
      type: Object,
      required: true
    }
  },
  components: {
    ElButton,
    ParticipateModal
  },
  setup() {

  },
  data: () => ({
    maxProgressWidth: 266,
    countAccumulated: 0,
    countLeft: 0,
    index: 0,
    showParticipateModal: false,
  }),
  watch: {
    business(newVal, oldVal) {
      if ((newVal.accumulated !== oldVal.accumulated)) {
        this.$nextTick(() => {
          // if (this.index>0) {
          this.countAccumulated = oldVal.accumulated
          this.countLeft = this.amountLeft(oldVal.amount, oldVal.accumulated)
          this.setAccumulated(newVal.accumulated)
          this.setLeft(this.amountLeft(newVal.amount, newVal.accumulated))
          // } else {
          //     this.countAccumulated = newVal.accumulated
          //     this.countLeft = this.amountLeft(newVal.amount, newVal.accumulated)
          //     this.index=1
          // }
        })
      }
    }
  },
  computed: {
    progress() {
      // return this.business.totalNeeded * this.business.exchangeInvestments
      return this.business.exchangeInvestments * 100 / this.business.totalNeeded
    },
    timeOfCompletion() {
      const currentDate = +Date.now()
      const createdTimestamp = this.business.createdTimestamp
      const progress = this.progress
      // console.log(progress, createdTimestamp, currentDate)
      // return createdTimestamp + (currentDate - createdTimestamp)/progress - currentDate
      return Math.max(0, (currentDate - createdTimestamp) / progress * (1 - progress))
    }
  },
  methods: {
    setAccumulated(val) {
      const obj = { n: this.countAccumulated }
      anime({
        targets: obj,
        n: val,
        round: 1,
        duration: 3000,
        easing: 'linear',
        update: () => {
          this.countAccumulated = obj.n
        }
      })
    },
    setLeft(val) {
      const obj = { n: this.countLeft }
      anime({
        targets: obj,
        n: val,
        round: 1,
        duration: 3000,
        easing: 'linear',
        update: () => {
          this.countLeft = obj.n
        }
      })
    },
    participate() {
      this.showParticipateModal = true
    },
    closeModals() {
      this.showParticipateModal = false
    },
    getTimeString(miliseconds) {
      if (miliseconds / 3600000 >= 1) {
        const hours = Math.ceil(miliseconds / 3600000)
        return localizeFilter('hours_short', [hours])
      } else {
        const minutes = Math.ceil(miliseconds / 60000)
        return localizeFilter('minutes_short', [minutes])
      }
    },
    amountLeft(amount, accumulated) {
      return Math.max(0, amount - accumulated)
    }
  },
  mounted() {
    this.countAccumulated = this.business.accumulated
    this.countLeft = this.amountLeft(this.business.exchangeInvestments, this.business.accumulated)
  }
})
</script>

