<template>
<div class="large-toggle-block">
<label class="title" v-if="title">{{title}}</label>
<div class="large-toggle-container">
    <div class="large-toggle left" :class="value==0 ? 'selected': 'default'" @click="toggle(0)">
        <label class="text" :class="value==0 ? 'selected': ''">{{$localizeFilter(variants[0])}}</label>
    </div>
    <div class="large-toggle right" :class="value==1 ? 'selected': 'default'" @click="toggle(1)">
        <label class="text" :class="value==1 ? 'selected': ''">{{$localizeFilter(variants[1])}}</label>
    </div>
</div>
</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  props: ['title','variants','selected'],
    watch: { 
            selected: function(newVal, oldVal) {
                if (newVal != null && newVal!= undefined) {
                this.value = this.variants.indexOf(newVal)
            }
        }
    },
    data: ()=> ({
        value: 0
    }),
    methods: {
        toggle(newVal) {
            this.value=newVal
            this.$emit('toggle', Number(this.value))
        }
    },
})
</script>

<style scoped>
 .large-toggle-block {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.large-toggle-container {
  display: inline-block;
  border: 1px solid #90CAFF;
  width: 400px;
  height: 48px;
  position: relative;
  margin: 0;
}

.large-toggle {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  content: ""!important;
  width: calc(50% + 2px);
  height:calc(100% + 2px);
  top: -1px;
  position: absolute;
  transition: all 0.1s linear;
  cursor: pointer;
}

.large-toggle.selected.left {
    background: #90CAFF;
}

.large-toggle.selected.right {
    background: #90CAFF;
}

.large-toggle * {
    cursor: pointer;
}

/* .large-toggle.default:hover label {
    font-weight: 700;
} */

.large-toggle.left {
      left: -1px;
}

.large-toggle.right {
      right: -1px;
}
.large-toggle-block {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.title {
    margin-right: 10px;
}

.title, .text {
    font-family: 'Play';
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #E4E4E4;

}

.text.selected {
    font-weight: 700;
    color: #101222;
}
