<template>
  <ElLink skin-off class="el-button" :style="clipPath" v-bind="$attrs">
    <slot></slot>
  </ElLink>
</template>

<script>
import ElLink from './ElLink.vue';

export default {
    props: {
        corners: {
            type: String,
            default: ""
        }
    },
    computed: {
        clipPath() {
            const RE = {
                tl: /tl\s*(?<h>\d+)\s+(?<v>\d+)/gi,
                tr: /tr\s*(?<h>\d+)\s+(?<v>\d+)/gi,
                br: /br\s*(?<h>\d+)\s+(?<v>\d+)/gi,
                bl: /bl\s*(?<h>\d+)\s+(?<v>\d+)/gi
            };
            const corners = {};
            for (let corner in RE) {
                let match;
                while ((match = RE[corner].exec(this.corners)) !== null) {
                    if (match.index === RE[corner].lastIndex)
                        RE[corner].lastIndex++;
                    corners[corner] = match.groups;
                }
            }
            const { tl = null, tr = null, br = null, bl = null } = corners;
            const style = {
                "clip-path": `polygon(${tl?.h && tl?.v ? `0 ${tl.v}px, ${tl.h}px 0` : "0 0"}, ${tr?.h && tr?.v ? `calc(100% - ${tr.h}px) 0, 100% ${tr.v}px` : "100% 0"}, ${br?.h && br?.v ? `100% calc(100% - ${br.v}px), calc(100% - ${br.h}px) 100%` : "100% 100%"}, ${bl?.h && br?.v ? `${bl.h}px 100%, 0 calc(100% - ${bl.v}px)` : "0 100%"})`
            };
            console.log({ style, corners });
            return style;
            /**
             * // no coners
             * polygon(0 0, 100% 0, 100% 100%, 0 100%);
             *
             * // all coners 20px X 20 px
             * polygon(
             *   0 20px, 20px 0,
             *   calc(100% - 20px) 0, 100% 20px,
             *   100% calc(100% - 20px), calc(100% - 20px) 100%
             *   20px 100%, 0 calc(100% - 20px)
             * );
             */
        }
    },
    components: { ElLink }
}
</script>

<style>
  .el-button {
    display: block;
    padding: 9px 35px;
    background: #90CAFF;
    border: 1px solid #90CAFF;
    box-shadow: none;
    font-family: 'Play';
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #101222;
  }
  .el-button:hover, .el-button:focus {
    background: linear-gradient(93.69deg, #90CAFF -2.68%, #72E2FA 112%);
    box-shadow: none;
  }
</style>
